  font-weight: 500;
  line-height: 1.1944444444;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

/*
    Import partial elements
*/
.btn {
  background-color: transparent;
  border: none;
  border-radius: 0;
  padding: 0;
  text-decoration: none;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  vertical-align: top;
  background-color: #ffffff;
  border-radius: 30px;
  border: 2px solid #070707;
  color: #070707;
  font-family: Gilroy, system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-weight: 700;
  font-size: 18px;
  line-height: 1.2222222222;
  max-width: 280px;
  padding: 8px 17px;
  /*
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  min-height: 40px;
  */
  transition: background-color 0.4s, border-color 0.4s, color 0.4s;
  /* .3s - такую запись stylelint не пропускает */
}
.btn--black-and-long {
  background: #070707;
  color: #ffffff;
  padding: 8px 27px;
}
.btn--black-and-long:hover:not(.btn:disabled), .btn--black-and-long:focus {
  background-color: #ffffff;
  color: #070707;
}
.btn--black-and-long:active {
  opacity: 0.5;
}
.btn--bg--wild-sand {
  background-color: #f6f6f6;
}
.btn:hover:not(.btn:disabled, .btn--black-and-long), .btn:focus:not(.btn--black-and-long) {
  background-color: #070707;
  color: #ffffff;
}
.btn:active {
  opacity: 0.7;
}
.btn:disabled {
  border: 2px solid #e5e5e5;
  color: #e5e5e5;
  cursor: no-drop;
}

