.underlined-link {
  position: relative;
  transition: color 0.5s;
  /*
      Так как transition не поддерживает плавный переход
      для text-decoration-style и border-bottom-style,
      сделал решение с псевдоэлементами и transition с opacity.

      Выглядит излишне, но теперь переходы по ссылкам
      смотрятся плавно.
  */
}
.underlined-link::before {
  border-bottom: #828282 dashed 1px;
  content: "";
  width: 100%;
  position: absolute;
  bottom: -3px;
  right: 0;
  transition: opacity 0.5s;
}
.underlined-link::after {
  border-bottom: #828282 solid 1px;
  content: "";
  opacity: 0;
  width: 100%;
  position: absolute;
  bottom: -3px;
  right: 0;
  transition: opacity 0.5s, border-bottom-color 0.5s;
}
.underlined-link:hover {
  color: #070707;
}
.underlined-link:hover::before {
  opacity: 0;
}
.underlined-link:hover::after {
  opacity: 1;
}
.underlined-link:active {
  color: #828282;
}
.underlined-link:active::after {
  border-bottom-color: #828282;
}
.underlined-link--grey-to-black::before {
  border-bottom-color: #828282;
}
.underlined-link--grey-to-black::after {
  border-bottom-color: #070707;
}
.underlined-link--black::before {
  border-bottom-color: #070707;
}
.underlined-link--black::after {
  border-bottom-color: #070707;
}
.underlined-link--white::before {
  border-bottom-color: #ffffff;
}
.underlined-link--white::after {
  border-bottom-color: #ffffff;
}
.underlined-link--white:hover {
  color: #ffffff;
}
.underlined-link--white:active {
  color: #828282;
}

