/* Общая стилизация заголовков */

@mixin titles-common {
    font-weight: $font-weight-bold;
    margin: 0;
}

/* Сброс стилей списков */

@mixin no-list {
    list-style: none;
    margin: 0;
    padding: 0;
}

/* Сброс стандартной стилизации кнопок */

@mixin btn-reset {
    background-color: transparent;
    border: none;
    border-radius: 0;
    padding: 0;
    text-decoration: none;
    cursor: pointer;
}

/* Сброс стандартной стилизации input */

@mixin input-reset {
    border: none;
    border-radius: 0;
    outline: none;
}

/* Скрытие стилей для стандартных фильтров */

@mixin custom-filters-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(0 0 0 0);
}

/* Cтили для кастомных фильтров */

@mixin custom-filters-visible {
    background-color: $color-white;
    border: 2px solid $color-black;
    border-radius: 5px;
    content: "";
    opacity: 0.1;
    width: 25px;
    height: 25px;
    position: absolute;
    top: 0;
    left: 0;
}

