.u-svg-wrapper {
    display: none;
}

.u-hidden {
    /* display: none !important; */

    display: none;
}

.visually-hidden:not(:focus):not(:active) {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
    white-space: nowrap;
}

.u-invisible {
    visibility: hidden;
}
